.App {
    text-align: left;
}

.precollapse::after {
    content: "";
}

.trigger {
    background-color: white;
    border: none;
}

.App-logo {
    height: 40vmin;
    pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
    .App-logo {
        animation: App-logo-spin infinite 20s linear;
    }
}

.Year {
    background-color: gray;
}

.App-header {
    background-color: #282c34;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
}

.App-link {
    color: #61dafb;
}

.input-warning {
    background-color: orange;
}

.input-error {
    background-color: #ff2a55;
}

.input-normal {
    background-color: white;
}

.error-modal {
    position: fixed;
    bottom: 0%;
    right: 0%;

    z-index: 1000;

    /*makes the content be the center*/
    /* transform: translate(-50%, -50%); */
}
.error-stack {
    display: flex;
    flex-direction: column-reverse;
    align-items: center;
    justify-content: flex-start;
}

.error-stack .error {
    border-radius: 1.5vh;
    border-style: solid;
    border-width: 0.1em;
    border-color: red;
    background-color: rgba(255, 0, 0, 0.5);
}

.error-stack .warning {
    border-radius: 1.5vh;
    border-style: solid;
    border-width: 0.1em;
    border-color: orange;
    background-color: rgba(255, 153, 0, 0.733);
}

.error-stack span {
    text-align: center;
    font-size: small;
    padding-top: 1em;
    padding-bottom: 1em;
    margin-top: 0.05em;
    margin-bottom: 0.05em;
}

.error-stack-container .error-counter {
    min-width: 10.9vw;
    text-align: center;
}

.error-stack-container {
    position: fixed;
    height: 100vh;
    width: 33vw;
    right: 0;
    top: 0;
    margin-right: 0.5vh;
    z-index: 1071;
    overflow-y: scroll;
}

@keyframes App-logo-spin {
    from {
        transform: rotate(0deg);
    }
    to {
        transform: rotate(360deg);
    }
}
